import React, { useEffect, useRef, useState } from "react";
import { Html5Qrcode } from "html5-qrcode";
import { useToast } from "@chakra-ui/react";

function QrScanner() {
  const [result, setResult] = useState("");
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);
  const toast = useToast();
  const qrCodeRegionId = "qr-reader";
  const html5QrCodeRef = useRef(null);

  useEffect(() => {
    const startScanner = async () => {
      try {
        html5QrCodeRef.current = new Html5Qrcode(qrCodeRegionId);
        const cameras = await Html5Qrcode.getCameras();

        if (cameras && cameras.length) {
          const cameraId = cameras[0].id;

          await html5QrCodeRef.current.start(
            cameraId,
            {
              fps: 10,
              qrbox: 250,
            },
            (decodedText) => {
              setResult(decodedText);
              toast({
                description: "QR Code Scanned Successfully!",
                status: "success",
                duration: 2000,
                isClosable: true,
              });

              html5QrCodeRef.current.stop().catch((err) =>
                console.error("Failed to stop scanner: ", err)
              );
            },
            (errorMessage) => {
              // Optional: log scan errors
              // console.log(`QR Code scan error: ${errorMessage}`);
            }
          );
          setLoading(false);
        } else {
          setError("No camera found on this device.");
          setLoading(false);
        }
      } catch (err) {
        setError("Unable to access the camera or start scanner.");
        setLoading(false);
        toast({
          description: "Unable to access the camera.",
          status: "error",
          duration: 3000,
          isClosable: true,
        });
      }
    };

    startScanner();

    return () => {
      if (html5QrCodeRef.current) {
        html5QrCodeRef.current.stop().catch(() => {});
      }
    };
  }, []);

  if (loading) {
    return <div className="text-center mt-20">Loading camera...</div>;
  }

  if (error) {
    return <div className="text-center mt-20 text-red-600">{error}</div>;
  }

  return (
    <div className="flex flex-col items-center justify-center h-screen bg-gray-100">
      <div
        id={qrCodeRegionId}
        style={{ width: "300px", height: "300px", border: "2px solid #0b85ff", borderRadius: "8px" }}
      ></div>

      <div className="mt-4 p-4 bg-white rounded shadow w-96 text-center">
        <p className="mb-2">{result || "Scan a QR Code using your camera..."}</p>
        <button
          onClick={() => navigator.clipboard.writeText(result)}
          disabled={!result}
          className="bg-green-600 text-white px-4 py-2 rounded mr-2 disabled:opacity-50"
        >
          Copy
        </button>
        <button
          onClick={() => setResult("")}
          className="bg-blue-600 text-white px-4 py-2 rounded"
        >
          Clear
        </button>
      </div>
    </div>
  );
}

export default QrScanner;
